.project-info {
  width: 45%;
  height: 100%;
  float: right;
  position: relative;
  display: flex;
  align-items: center;
  text-align: left;
  margin-top: 16%;
  margin-right: 4%;
}

.project-info img {
  width: 60%;
  margin-left: -2vw;
}

.header {
  display: flex;
  flex-direction: column;
  float: right;
}

.header p {
  font-size: 12px;
  line-height: 16px;
}

.close-modal-btn {
  background: none;
  color: #ffffff;
  font: 28px/1.9em 'Montserrat', sans-serif;
  content: '\2715';
  z-index: 1000;
  top: 0;
  left: 0;
}
.close-modal-btn:after {
  background: none;
  color: #ffffff;
  font: 28px/1.9em 'Montserrat', sans-serif;
  content: '\2715';
  z-index: 1000;
}

.close-modal-btn:hover {
  background: none;
  color: #00ffff;
}

.header a {
  color: #191b1c;
}
.header a:hover {
  color: #00ffff;
}

@media only screen and (max-width: 480px) {
  .project-info {
    width: 85%;
    height: 100%;
    float: none;
    position: relative;
    display: flex;
    align-items: center;
    text-align: left;
    margin-top: 86%;
    margin-right: 0%;
  }
}
